.cur-title {
    text-align: left;
    margin-top: 10px;
    margin-left: 20px;
    margin-bottom: 20px;
    font-family: Ubuntu;
    font-size: 25px;
    opacity: 0.6;
    color: var(--title);
  }
  
  .cur-category-box {
    border: 1px solid;
    border-color: var(--border);
    display: flex;
    overflow-y: auto;
width: 100%;
    max-height: 400px;
    border-bottom: none;
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
  
    max-height: 400px;
    border-radius: 1px;
  }
  
  .cur-search-box {
    border-color: var(--border);
    height: fit-content;
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    background: var(--header);
  }
  
  .cur-search {
    width: 400px;
    height: 30px;
    border-width: 2px;
    border-color: var(--border);
    outline-width: 0;
    border-style: solid;
    border-radius: 5px;
    align-self: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
  }
  
  .cur-search:hover,
  .search:focus {
    background-color: var(--focus);
  }
  
  .cur-search-text {
    text-overflow: clip;
    height: 30px;
    width: 370px;
    border-width: 0;
    outline-width: 0;
    border-radius: 2px;
    align-self: center;
    text-align: left;
    margin-inline-start: 5px;
    box-sizing: border-box;
    font-family: "Ubuntu";
    font-size: medium;
    display: flex;
    flex-direction: row;
    background-color: rgba(0, 0, 0, 0);
  }
  
  .cur-search-text::placeholder {
    color: var(--search);
    opacity: 0.8;
    font-size: medium;
  }
  
  .cur-search-text:focus::-webkit-input-placeholder {
    color: transparent;
  }
  
  .cur-add {
    border-width: 0.5px;
    border-color: var(--border);
    outline-width: 0;
    border-style: solid;
    border-radius: 5px;
    align-self: center;
    justify-content: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    cursor: pointer;
    background-color: #018bb6;
    border: 1px solid transparent;
    transition: background-color 1s;
  }
  
  .cur-add:hover,
  .add:focus {
    background-color: var(--button-background-focus);
  }
  
  p {
    font-family: "Ubuntu";
    justify-self: center;
    align-self: center;
    padding-right: 5px;
    font-size: medium;
    color: white;
  }
  
  .cur-holder {
    display: flex;
    flex: 1;
    align-items: center;
    flex-direction: row;
    justify-content: flex-start;
    padding: 5px;
    overflow: auto;
  }
  
  .cur-remove-items {
    max-height: 31.7rem;
  }
  
  .cur-table-holder {
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: row;
    justify-content: flex-end;
    padding: 5px;
    border-bottom: 1px solid var(--border);
  }
  
  .cur-head-holder {
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: row;
    justify-content: flex-end;
    padding: 5px;
    border-bottom: 1px solid var(--border);
    background: var(--header);
  }
  
  .cur-table-holder:hover {
    background-color: var(--table-hover);
  }
  
  .cur-table {
    display: flex;
    flex: 1;
  
    flex-direction: column;
    justify-content: flex-start;
  }
  
  .cur-name {
    font-family: "Ubuntu";
    width: 350px;
    text-align: center;
    display: flex;
  }
  .cur-symbol {
    font-family: "Ubuntu";
    width: 350px;
    justify-content: center;
    text-align: center;
    display: flex;
  }
  .cur-price {
    font-family: "Ubuntu";
    width: 200px;
    text-align: center;
    display: flex;
  }
  
  .cur-group {
    font-family: "Ubuntu";
    width: 200px;
    text-align: center;
    display: flex;
  }
  
  .cur-head-name {
    font-family: "Ubuntu";
    width: 350px;
    text-align: center;
    display: flex;
    height: 30px;
  }
  
  .cur-sort {
    cursor: pointer;
    align-self: center;
  }
  
  .cur-group1 {
    font-family: "Ubuntu";
    width: 40px;
    justify-content: flex-end;
    display: flex;
  }
  
  .cur-modify-icon:hover {
    color: var(--color-scheme);
  }
  
  .cur-delete-icon:hover {
    color: var(--color-scheme);
  }
  
  .cur-trans-enter {
    opacity: 0;
  }
  
  .cur-trans-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  
  .cur-trans-exit {
    opacity: 1;
  }
  
  .cur-trans-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
  }
  
  .modal-cur-wrapper {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .modal-cur {
    height: 250px;
    width: 600px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .modal-cur-header {
    height: 60px;
    background-color: var(--color-scheme-dark);
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: "Ubuntu";
    color: white;
    padding-left: 20px;
    padding-right: 20px;
    user-select: none;
  }
  
  .modal-cur-close {
    cursor: pointer;
    height: 20px;
    width: 20px;
    color: white;
  }
  
  .modal-cur-close:hover {
    color: lightgray;
  }
  
  .modal-cur-body {
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    max-height: 300px;
    user-select: none;
    
  }
  
  .modal-cur-footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10px;
    height: 50px;
  }
  
  .modal-cur-save {
    background: var(--color-scheme);
    border-radius: 5px;
    width: 70px;
    height: 30px;
    cursor: pointer;
    font-family: "Ubuntu";
    color: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
    transition: opacity 500ms ease-in-out;
  }
  
  .modal-cur-save:hover {
    opacity: 1;
  }
  
  .modal-cur-subtitle {
    min-height: 40px;
    background-color: var(--color-scheme);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    font-family: "Ubuntu";
    color: white;
    padding-left: 20px;
    user-select: none;
  }
  
  .modal-cur-form {
    font-family: "Ubuntu";
  }
  
  .modal-cur-desc {
    margin: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .modal-cur-desc-hor {
    margin: 10px;
    display: flex;
    flex-direction: row;
    padding-right: 10px;
    padding-top: 5px;
    align-items: center;
  }
  
  .modal-check {
    align-self: center;
    margin-right: 20px;
  }
  
  .modal-cur-price,
  .modal-cur-function {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .modal-cur-desc-input {
    margin-top: 10px;
    width: 300px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid;
    outline: none;
    font-family: "Ubuntu";
    padding: 10px;
    margin-right: 10px;
  }
  
  .modal-cur-desc-input::placeholder {
    font-family: "Ubuntu";
  }
  
  .modal-cur-desc-input:focus::-webkit-input-placeholder {
    color: transparent;
  }
  
  .modal-cur-price-input {
    margin-top: 10px;
    width: 200px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid;
    outline: none;
    font-family: "Ubuntu";
    padding: 10px;
    margin-right: 10px;
  }
  
  .modal-cur-function-input {
    margin-top: 10px;
    width: 300px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid;
    outline: none;
    font-family: "Ubuntu";
    padding: 5px;
    margin-right: 10px;
  }
  
  .modal-cur-print-input {
    margin-top: 10px;
    width: 150px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid;
    outline: none;
    font-family: "Ubuntu";
    padding: 5px;
    margin-right: 10px;
  }
  
  .modal-cur-print-input:focus,
  .modal-cur-print-input:hover,
  .modal-cur-function-input:focus,
  .modal-cur-function-input:hover,
  .modal-cur-price-input:hover,
  .modal-cur-price-input:focus,
  .modal-cur-desc-input:focus,
  .modal-cur-desc-input:hover,
  .modal-check:focus,
  .modal-check:hover {
    box-shadow: 2px 2px var(--color-scheme-light);
  }
  
  .modal-cur-function-option {
    font-family: "Ubuntu";
  }
  
  .modal-cur-price-input::placeholder {
    font-family: "Ubuntu";
  }
  
  .modal-cur-price-input:focus::-webkit-input-placeholder {
    color: transparent;
  }
  
  .modal-spacer {
    border-top: 1px solid var(--color-scheme-dark);
  }
  
  .modal-cur-branch {
    background: var(--color-scheme);
    border-radius: 5px;
    width: 250px;
    height: 30px;
    cursor: pointer;
    font-family: "Ubuntu";
    color: white;
    text-align: center;
    padding-top: 5px;
    opacity: 0.8;
    transition: opacity 500ms ease-in-out;
    margin-top: 20px;
    margin-right: 20px;
  }
  
  .modal-cur-branch:hover {
    opacity: 1;
  }
  
  @keyframes inAnimation {
    0% {
      opacity: 0;
      visibility: hidden;
    }
    100% {
      opacity: 1;
      visibility: visible;
    }
  }
  
  @keyframes outAnimation {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      visibility: hidden;
    }
  }
  
  @keyframes downAnimation {
    0% {
      transform: translateY(-20%);
    }
    100% {
      transform: translateY(0%);
    }
  }
  
  @keyframes upAnimation {
    0% {
      transform: translateY(0%);
    }
    100% {
      transform: translateY(-20%);
    }
  }
  