.top-bar {
  background-color: var(--color-scheme);
  display: flex;
  min-height: 60px;

  width: 100%;
  flex-direction: row;
  align-items: center;
  font-family: "Ubuntu";
  justify-content: flex-end;
}

.top-user-bar{
  
  display:flex;
  min-height: 60px;
  width: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;

}

.rest {
  margin-left: 5vw;
  color: white;
}

.cont {
  display: flex;
  overflow-x: hidden;
  flex-direction: column;
}

.hm {
  display: flex;
  flex-direction: row;
}
.user-dropdown{
  width: 100%;
  align-items: center;
  display: flex;
  margin: 3px;
  flex-direction: column;
  background-color: lightgrey;
  border: 1px solid lightgray;
  border-radius: 3px;
}
.back {
  margin-right: 25vw;
  color: white;
}
.user-sub-dropdown:hover{
  background-color: var(--table-hover);
}

.user-sub-dropdown{
  width: 100%;

  display: flex;
  height: 40px;
  align-items: center;
  justify-content: center;
}

.icons {
  color: white;
  margin-right: 3vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 25vw;
  user-select: none;
}

.user-holder {
  display: flex;
  min-width: 250px;
  flex-direction: row;
  cursor: pointer;
  align-items: center;
}

.side-bar {
  display: flex;
  flex-direction: column;
  width: 15%;
  min-width: 200px;
  font-family: "Ubuntu";
  min-height: 200px;
  overflow-y: auto;
  height: 100%;
}

.index {
  display: flex;
  flex-direction: column;
}

.index0 {
  display: flex;
  flex-direction: row;
  height: 6vh;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.2);
  transition: background-color 300ms ease-in;
}

.index1 {
  display: flex;
  flex-direction: row;
  height: 0;
  align-items: center;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 10px;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.15);
  transition: background-color 300ms ease-in;
  opacity: 0;
  color: black;
  text-decoration: none;
}

.index2 {
  display: flex;
  flex-direction: row;
  height: 0;
  align-items: center;
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 10px;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.1);
  transition: background-color 300ms ease-in;
  opacity: 0;
  color: black;
  text-decoration: none;
}

.index0:hover,
.index1:hover,
.index2:hover {
  background-color: var(--table-hover);
}

.index1:active,
.index2:active {
  color: black;
}
.appHeader:hover{
  background-color: red;
}

@keyframes openAnimation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(90deg);
  }
}

@keyframes closeAnimation {
  0% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes slideOpenAnimation {
  0% {
    height: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
    height: 6vh;
  }
}

@keyframes slideCloseAnimation {
  0% {
    opacity: 1;
    height: 6vh;
  }
  100% {
    opacity: 0;
    height: 0;
  }
}
